import React from 'react';
import {
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    SelectChangeEvent,
} from '@mui/material';

interface OperatorFilterProps {
    filterType: string;
    onFilterChange: (event: SelectChangeEvent<string>) => void;
}

const OperatorFilter: React.FC<OperatorFilterProps> = ({
    filterType,
    onFilterChange,
}) => {
    return (
        <FormControl variant="outlined">
            <InputLabel id="filter-select-label"></InputLabel>

            <Select
                labelId="filter-select-label"
                value={filterType}
                onChange={onFilterChange}
                label=""
            >
                <MenuItem value=""></MenuItem>
                <MenuItem value="isWorking"></MenuItem>
                <MenuItem value="notWorking"> </MenuItem>
                <MenuItem value="today"></MenuItem>
                <MenuItem value="yesterday"></MenuItem>
                <MenuItem value="thisMonth"> </MenuItem>
                <MenuItem value="lastMonth"> </MenuItem>
            </Select>
        </FormControl>
    );
};

export default OperatorFilter;
