import React from 'react';
import {
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    SelectChangeEvent,
} from '@mui/material';

interface OperatorSortProps {
    sortType: string;
    onSortChange: (event: SelectChangeEvent<string>) => void;
}

const OperatorSort: React.FC<OperatorSortProps> = ({
    sortType,
    onSortChange,
}) => {
    return (
        <FormControl variant="outlined">
            <InputLabel id="sort-select-label"></InputLabel>

            <Select
                labelId="sort-select-label"
                value={sortType}
                onChange={onSortChange}
                label=""
            >
                <MenuItem value=" "> </MenuItem>
                <MenuItem value="name-asc"> A-</MenuItem>
                <MenuItem value="name-desc"> -A</MenuItem>
                <MenuItem value="isWorking"></MenuItem>
                <MenuItem value="notWorking"> </MenuItem>
                <MenuItem value="date-desc"></MenuItem>
                <MenuItem value="date-asc"></MenuItem>
            </Select>
        </FormControl>
    );
};

export default OperatorSort;
